body{
  background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.App {
  text-align: center;
  font-family: 'Kaisei Tokumin', serif;
}

.Cards{
  height: 100px;
  margin: 0 7px;
  width: 20%;
  border: 2px solid black;
  box-sizing: border-box;
}

.details{
  display: flex;
  justify-content: center;
}

.selectarea{
  margin:  15px 0px;
}

@media screen and (max-width: 900px) {
  .Cards{
    height: 100px;
    margin: 0 4px;
    width: 20%;
    border: 2px solid black;
    box-sizing: border-box;
  }
}